const AWS = require('aws-sdk');
import rp from 'request-promise';

class TranscriptionService {

    constructor() {
        this.transcribe = new AWS.TranscribeService();
    }

    getTranscriptForJobName(jobName) {
        let params = {
            TranscriptionJobName: jobName
        };
        return this.transcribe
            .getTranscriptionJob(params).promise()
            .then(transcriptionJob => {
                let options = {
                    method: 'GET',
                    uri: transcriptionJob.TranscriptionJob.Transcript.TranscriptFileUri,
                    json: true
                };
                return rp(options).then(transcript => {
                    return {
                        transcriptionJob,
                        transcripts: transcript.results.transcripts || null
                    };
                });
            })
            .catch(err => {
                console.log(err);
            });
    }
}

export {TranscriptionService};